/* basic formatting */

body {font-size: 20px; font-family: 'Lato'; line-height: 32px;}

strong, b { font-weight: 700; }

h1 {font-size: 32px; font-weight: bold;}
h2 {font-size: 32px; font-weight: bold;}
h3 {font-size: 24px; font-weight: 600;}

h1 a, h2 a { text-decoration: none;}

/* grid layout */

section{
  display: block;
}

.grid {
  display: grid;
  grid-template-columns: auto repeat(6, minmax(0px, 136px)) auto;
  grid-gap: 16px;
}

.grid .test {
  grid-column-start: 2;
  grid-column-end: 8;
  background: green;
}

.grid .header {
  grid-column-start: 2;
  grid-column-end: 8;
  background: pink;
}

.grid .bio-in-brief {
  grid-column-start: 2;
  grid-column-end: 6;
  background: pink;
}

.grid .bio-image{
  grid-column-start: 6;
  grid-column-end: 8;
  background: pink;
}

.grid .bio-image img{
  max-width:100%;
}


.grid .lean-ux-explained{
  grid-column-start: 1;
  grid-column-end: 9;
  background: pink;

  display: grid;
  grid-template-columns: auto repeat(6, minmax(0px, 136px)) auto;
  grid-gap: 16px;
}

.lean-ux-explained .content{

  grid-column-start: 2;
  grid-column-end: 8;
  background: green;
}

.grid .articles {
  grid-column-start: 2;
  grid-column-end: 8;
  background: pink;
}

.grid .case-studies {
  grid-column-start: 2;
  grid-column-end: 8;
  background: pink;
}
